@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap");

body {
  margin: 0px;
  max-width: 100%;
  height: 100%;
  overflow-y: hidden;
  font-family: Montserrat;
}

.s1 {
  height: 100%;
}

.s1 {
  height: 100%;
  position: relative;
  background-color: rgb(12, 10, 30);
}

.s3,
.s4 {
  z-index: 1;
}

#tsparticles {
  z-index: 0;
}

.scroll {
  height: 100vh;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
}

.scroll::-webkit-scrollbar {
  display: none;
}

.scroll section {
  scroll-snap-align: start;
  height: 100vh;
}

#tsparticles {
  opacity: 0.7;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.s4 {
  height: 100%;
}

/* HEADER */

.header-container {
  position: fixed;
  padding: 0px;
  margin: 0px;
  top: 0px;
  left: 50px;
  z-index: 999999;
  justify-content: left;
  color: white;
  font-size: x-large;
  pointer-events: none;
}

.header-container .three {
  /* border: 10px solid black;
  background-color: black; */
  color: white;
  background-color: black;
}

.header-container .four {
  color: black;
}

.header-container .two {
  color: black;
}

.header-container p {
  font-weight: bolder;
  animation: headerChange 300ms ease-in-out forwards;
}

@keyframes headerChange {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

/* WELCOME PAGE */

.welcome-container {
  width: 100%;
  height: 90vh;
}

.welcome-container .main-page {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  align-items: center;
  justify-content: center;
  height: 90vh;
  color: white;
  gap: 20px;
  position: relative;
}

.left-up {
  z-index: 1;
  grid-column: 1/2;
  grid-row: 1/3;
  display: flex;
  align-items: flex-end;
  justify-content: end;
  text-align: end;
  flex-direction: column;
  pointer-events: none;
  font-size: x-large;
}

.stack {
  margin: 0px;
  font-size: large;
}

.right {
  z-index: 1;
  grid-column: 2/3;
  grid-row: 1/3;
  display: flex;
  pointer-events: none;
}

.right h1 {
  font-size: xx-large;
}

.typing {
  color: rgb(55, 55, 255);
  margin-left: 10px;
  position: relative;
}

.typing::after {
  content: "_";
  position: absolute;
  right: -20px;
  animation: blink 0.5s infinite ease;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.stack span {
  color: rgb(55, 55, 255);
  font-size: larger;
}

/* EXPERIENCE */

.experience-container {
  width: 100%;
  height: 90vh;
  padding-top: 10vh;
  position: relative;
}

.tables-container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 80%;
}

.title1,
.title2,
.title3,
.title4,
.certificates {
  border: 1px solid rgb(16, 8, 91);
  background-color: rgb(12, 10, 30);
  color: white;
  font-weight: bold;
  cursor: pointer;
  position: relative;
}

.title1:hover,
.title2:hover,
.title3:hover,
.title4:hover,
.certificates:hover {
  border: 1px solid rgb(12, 10, 30);
  background-color: rgb(16, 8, 91);
  color: white;
  font-weight: bold;
  cursor: pointer;
  text-decoration: underline;
}

.title1 h5,
.title2 h5,
.title3 h5,
.title4 h5 {
  margin: 0;
  position: absolute;
  top: 50%;
  right: 3%;
  transform: translate(0%, -50%);
  pointer-events: inherit;
}

.title1 h2,
.title2 h2,
.title3 h2,
.title4 h2,
.certificates h3 {
  font-size: medium;
  padding-left: 15px;
  text-align: center;
  pointer-events: none;
}

.tables-container .title {
  display: flex;
  padding: 0px 15px;
  justify-content: space-between;
}

.certificatestext h4 a {
  margin: 0px;
  text-decoration: none;
  color: black;
}

.certificatestext h4 a:hover {
  text-decoration: underline;
}

.tables-container .opis {
  font-size: normal;
  text-align: justify;
  margin: 0px;
  padding: 0px 15px;
  margin-bottom: 15px;
  padding-bottom: 15px;
  animation: growDown 300ms ease-in-out forwards;
}

.text1,
.text2,
.text3,
.text4,
.certificatestext {
  background-color: white;
  animation: growDown 300ms ease-in-out forwards;
  border: 1px solid black;
}

.certificatestext h4 a {
  text-decoration: none;
}

.certificatestext h4 {
  margin: 0px 0px 15px 15px;
}

.certificatestext {
  margin-bottom: 15px;
  padding-bottom: 1px;
  padding-top: 15px;
  animation: growDown 300ms ease-in-out forwards;
  text-align: center;
}

@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}

/* PROJECTS */

.projects-container {
  max-width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0);
}

.projects-grid {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 33% 32% 33%;
  gap: 5px;
  position: relative;
}

.sklep {
  grid-column: 1/3;
  grid-row: 1/3;
  background-color: rgba(255, 255, 255, 0.634);
}

.flagi {
  grid-column: 3/5;
  grid-row: 1/2;
  background-color: rgba(255, 255, 255, 0.574);
}

.minutnik {
  background-color: rgba(255, 255, 255, 0.574);
  grid-column: 5/6;
  grid-row: 1/2;
}

.drums {
  background-color: rgba(128, 128, 128, 0.574);
  grid-column: 3/4;
  grid-row: 2/3;
}

.pokedex {
  background-color: rgba(128, 128, 128, 0.574);
  grid-column: 4/6;
  grid-row: 2/4;
}

.kalkulator {
  background-color: rgba(128, 128, 128, 0.574);
  grid-column: 1/2;
  grid-row: 3/4;
}

.maze {
  background-color: rgba(128, 128, 128, 0.574);
  grid-column: 2/4;
  grid-row: 3/4;
}

.sklep img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.flagi img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.minutnik img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.kalkulator img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.drums img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.maze img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.pokedex img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.projects-grid div {
  background-color: black;
  position: relative;
}

.projects-grid img {
  opacity: 0.6;
}

.projects-grid img:hover {
  opacity: 1;
  animation: projekty 0.3s ease;
  cursor: pointer;
}

.projects-grid p {
  pointer-events: none;
}

@keyframes projekty {
  0% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

.details {
  font-size: normal;
  color: white;
  position: absolute;
  bottom: 5px;
  right: 5px;
  border: 5px solid black;
  background-color: black;
}

/* CARD */

.project-background {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

.card-container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: rgb(12, 10, 30);
  width: 70%;
  height: 85%;
  color: white;
}

.card-grid {
  display: grid;
  grid-template-columns: 3fr 2fr;
  height: 100%;
  gap: 2px;
}

.cardimg {
  width: 100%;
  height: 100%;
}

.cardimg img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.close {
  text-align: end;
}

.text-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}
.projectname h2 {
  text-align: center;
  width: 100%;
}

.projectstack {
  text-align: center;
}

.buttons {
  align-self: flex-end;
  width: 100%;
  position: absolute;
  bottom: 1px;
  right: 1px;
}

.buttons button {
  width: 50%;
  height: 10vh;
  border: 1px solid rgba(0, 0, 0, 0.621);
  background-color: rgb(56, 56, 56);
  color: white;
  font-family: Montserrat;
  font-size: large;
}

.buttons button:hover {
  text-decoration: underline;
  background-color: rgb(12, 10, 30);
  cursor: pointer;
  border: none;
}

.text-container span {
  color: rgb(55, 55, 255);
  font-size: large;
}

/* CONTACTS */

.contact-container {
  width: 100%;
  height: 100%;
}

.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row: 1fr;
  height: 100%;
}

.contactleft {
  background-color: white;
  width: 100%;
  height: 100%;
  grid-column: 1/2;
  z-index: 1;
}

.contactright {
  background-color: rgb(12, 10, 30);
  width: 100%;
  height: 100%;
  grid-column: 2/3;
}

.contactheader {
  height: 100%;
  width: 100%;
  position: relative;
}

.contactheader h1 {
  margin: 0;
  position: absolute;
  top: 40%;
  right: 5%;
  transform: translate(0%, -50%);
  font-size: 8vh;
  color: rgb(12, 10, 30);
  font-weight: bolder;
}

.contactright img {
  filter: invert(100%);
  height: 10vh;
  width: auto;
  padding-right: 5vh;
}

.contactright h2 {
  color: white;
  text-decoration: none;
}

.contactright a {
  text-decoration: none;
}

.contactright a:hover {
  text-decoration: underline;
  color: rgb(55, 55, 255);
}

.contactright {
  padding-left: 10vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.linkedin,
.github,
.mail {
  display: flex;
  margin-top: 5vh;
}

/* RWD 1024 */

/* WELCOME PAGE */

@media (max-width: 1024px) {
  .welcome-container {
    display: flex;
    height: 100%;
    align-items: center;
  }

  .welcome-container .main-page {
    display: flex;
    flex-direction: column-reverse;
  }
}

/* CONTACTS */

@media (max-width: 1024px) {
  .contact-grid {
    display: grid;
    grid-template-rows: 1fr 2fr;
    grid-template-columns: none;
    height: 100%;
  }

  .contactleft {
    grid-row: 1/2;
    grid-column: 1/3;
  }

  .contactright {
    grid-row: 2/3;
    grid-column: 1/3;
  }
}

/* RWD 768 */

/* WELCOME */

@media (max-width: 768px) {
  .left-up h2 {
    font-size: large;
  }

  .right h1 {
    font-size: large;
  }

  .typing::after {
    content: "_";
    position: absolute;
    right: -10px;
    animation: blink 0.5s infinite ease;
  }
}

/* DOŚWIADCZENIE */

@media (max-width: 768px) {
  .tables-container {
    width: 100%;
  }

  .title1 h2,
  .title2 h2,
  .title3 h2,
  .title4 h2,
  .certificates h3 {
    font-size: medium;
    pointer-events: none;
    text-align: left;
    font-size: small;
  }

  .title1 h5,
  .title2 h5,
  .title3 h5,
  .title4 h5,
  .certificates h5 {
    font-size: x-small;
  }

  .experience-container .opis {
    font-size: smaller;
  }
}

/* PROJEKTY */

@media (max-width: 1024px) {
  .projects-grid {
    display: grid;
    grid-template-columns: 33% 32% 33%;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    height: 100%;
    width: 100%;
  }

  .sklep {
    background-color: rgba(255, 255, 255, 0.634);
    grid-column: 1/4;
    grid-row: 1/4;
  }

  .flagi {
    background-color: rgba(255, 255, 255, 0.574);
    grid-column: 1/3;
    grid-row: 4/5;
  }

  .minutnik {
    background-color: rgba(255, 255, 255, 0.574);
    grid-column: 3/4;
    grid-row: 6/8;
  }

  .drums {
    background-color: rgba(128, 128, 128, 0.574);
    grid-column: 1/2;
    grid-row: 5/6;
  }

  .pokedex {
    background-color: rgba(128, 128, 128, 0.574);
    grid-column: 1/3;
    grid-row: 6/8;
  }

  .kalkulator {
    background-color: rgba(128, 128, 128, 0.574);
    grid-column: 3/4;
    grid-row: 4/5;
  }

  .maze {
    background-color: rgba(128, 128, 128, 0.574);
    grid-column: 2/4;
    grid-row: 5/6;
  }

  .projectsspan {
    display: none;
  }
}

/* PROJECT CARD */

@media (max-width: 1024px) {
  .card-grid {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 1fr 1fr;
  }
}

/* CONTACT */

@media (max-width: 1024px) {
  .contactright {
    padding-left: 2vh;
  }

  .contactright .contacttext {
    display: flex;
    height: 100%;
  }

  .contactright h2 {
    font-size: small;
    padding-top: 2.5vh;
  }

  .contactleft .contactheader {
    position: relative;
    width: 100%;
  }

  .contactheader h1 {
    width: 100%;
    text-align: center;
    font-size: 6vh;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
